import { Component, ViewChild, ElementRef } from '@angular/core';
import * as d3 from 'd3';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ng-d3-graph-editor';
  @ViewChild('graphContainer') graphContainer: ElementRef;

  width = 960;
  height = 600;
  colors = d3.scaleOrdinal(d3.schemeCategory10);

  svg: any;
  force: any;
  path: any;
  circle: any;
  drag: any;
  dragLine: any;

  selectedNode = null;
  selectedLink = null;
  mousedownLink = null;
  mousedownNode = null;
  mouseupNode = null;

  lastNodeId = 2;
  // only respond once per keydown
  lastKeyDown = -1;

  nodes = [
    { id: 0 },
    { id: 1},
    { id: 2 }
  ];
  links = [
    { source: this.nodes[0], target: this.nodes[1], left: false, right: true },
    { source: this.nodes[1], target: this.nodes[2], left: false, right: true }
  ];

  ngAfterContentInit() {
    const rect = this.graphContainer.nativeElement.getBoundingClientRect();
    this.width = rect.width;
    this.svg = d3.select('#graphContainer')
      .attr('oncontextmenu', 'return false;')
      .attr('width', this.width)
      .attr('height', this.height);
    this.force = d3.forceSimulation()
      .force('link', d3.forceLink().id((d: any) => d.id).distance(150))
      .force('charge', d3.forceManyBody().strength(-500))
      .force('x', d3.forceX(this.width / 2))
      .force('y', d3.forceY(this.height / 2))
      .on('tick', () => this.tick());
      
    this.svg.append('svg:defs').append('svg:marker')
      .attr('id', 'end-arrow')
      .attr('viewBox', '0 -5 10 10')
      .attr('refX', 6)
      .attr('markerWidth', 3)
      .attr('markerHeight', 3)
      .attr('orient', 'auto')
      .append('svg:path')
      .attr('d', 'M0,-5L10,0L0,5')
      .attr('fill', '#000');

    
    this.path = this.svg.append('svg:g').selectAll('path');
    this.circle = this.svg.append('svg:g').selectAll('g')
    .on('click', d => {
      console.log('-----circle---', d);
      this.click(d, 1, null);
    });
   
    this.restart();
  }
  clickCircle(source){
    console.log('----');
  }
  // update force layout (called automatically each iteration)
  tick() {
    // draw directed edges with proper padding from node centers
    this.path.attr('d', (d: any) => {
      const deltaX = d.target.x - d.source.x;
      const deltaY = d.target.y - d.source.y;
      const dist = Math.sqrt(deltaX * deltaX + deltaY * deltaY);
      const normX = deltaX / dist;
      const normY = deltaY / dist;
      const sourcePadding = d.left ? 17 : 12;
      const targetPadding = d.right ? 17 : 12;
      const sourceX = d.source.x + (sourcePadding * normX);
      const sourceY = d.source.y + (sourcePadding * normY);
      const targetX = d.target.x - (targetPadding * normX);
      const targetY = d.target.y - (targetPadding * normY);
      return `M${sourceX},${sourceY}L${targetX},${targetY}`;
    });
    this.circle.attr('transform', (d) => `translate(${d.x},${d.y})`)
    .on('click', d => {
      console.log('-----circle---', d);
      this.click(d, 1, null);
    });
  }

  resetMouseVars() {
    this.mousedownNode = null;
    this.mouseupNode = null;
    this.mousedownLink = null;
  }

  // update graph (called when needed)
  restart() {
    // path (link) group
    this.path = this.path.data(this.links);

    // update existing links
    this.path.classed('selected', (d) => d === this.selectedLink)
      .style('marker-start', (d) => d.left ? 'url(#start-arrow)' : '')
      .style('marker-end', (d) => d.right ? 'url(#end-arrow)' : '');

    // remove old links
    this.path.exit().remove();

    // add new links
    this.path = this.path.enter().append('svg:path')
      .attr('class', 'link')
      .classed('selected', (d) => d === this.selectedLink)
      .style('marker-start', (d) => d.left ? 'url(#start-arrow)' : '')
      .style('marker-end', (d) => d.right ? 'url(#end-arrow)' : '')
      .merge(this.path);

    // circle (node) group
    // NB: the function arg is crucial here! nodes are known by id, not by index!
    this.circle = this.circle.data(this.nodes, (d) => d.id);

    //this.circle.on('click', () => this.clickCircle());
    // update existing nodes (reflexive & selected visual states)
    this.circle.selectAll('circle')
      .style('fill', (d) => (d === this.selectedNode) ? d3.rgb(this.colors(d.id)).brighter().toString() : this.colors(d.id))
      //.classed('reflexive', (d) => d.reflexive);

    // remove old nodes
    this.circle.exit().remove();

    // add new nodes
    const g = this.circle.enter().append('svg:g');

    g.append('svg:circle')
      .attr('class', 'node')
      .attr('r', 12)
      .style('fill', (d) => (d === this.selectedNode) ? d3.rgb(this.colors(d.id)).brighter().toString() : this.colors(d.id))
      .style('stroke', (d) => d3.rgb(this.colors(d.id)).darker().toString())
    // show node IDs
    g.append('svg:text')
      .attr('x', 0)
      .attr('y', 4)
      .attr('class', 'id')
      .text((d) => d.id);
      g.append('svg:text')
      .attr('x', 33)
      .attr('y', 32)
      .attr('class', 'id')
      .text((d) => "node number" + " " + d.id); 

    this.circle = g.merge(this.circle);

    // set the graph in motion
    this.force
      .nodes(this.nodes)
      .force('link').links(this.links);

     this.force.alphaTarget(0.3).restart();
  }

  click(dataItem: any, value: any, source: any) {
    console.log('dataItem', dataItem);
    this.svg.classed('active', true);
    if (d3.event.ctrlKey || this.mousedownNode || this.mousedownLink) return;
    const point = d3.mouse(d3.event.currentTarget);
    // const point = d3.1mouse(this);
    const id = dataItem != undefined ? dataItem.id : ++this.lastNodeId+1;
    const val = value != undefined ? value : 0;
    
    const nd = this.nodes[dataItem.id+1];
    const ln = this.links[dataItem.id+1];
        this.nodes.filter(d => {
      if(d.id === dataItem.id+1){
        console.log('1. matched', d)
        this.nodes.splice(d.id, 1);        
      }
      
    })
    this.links.filter(d => {
      console.log('linkkkk ', d.source.id);
      console.log('linkkkk2 ', dataItem.id);
        if(d.source.id === dataItem.id){
          this.links.splice(d.source.id, 1);
          console.log('matched', d)
        }
    })

    console.log('nd-->', nd);
    const node = { id: this.nodes.length+1 , reflexive: false, x: point[0], y: point[1] };
    const link = { source: node, target: this.nodes[dataItem.id], left: false, right: true };
    
    this.nodes.push(node);
    this.links.push(link);
    
    const node2 = { id: this.nodes.length+1, reflexive: false, x: point[0], y: point[1] };
    const link2 = { source: node2, target: node, left: false, right: true };

    this.nodes.push(node2);
    this.links.push(link2);

    const node3 = { id: nd.id, reflexive: false, x: point[0], y: point[1] };
    const link3 = { source: node3, target: node2, left: false, right: true };

     this.nodes.push(node3);
     this.links.push(link3);
    this.restart();
  }


  
}
